<template>
   <div class="container">
    <h1 class="section-title">
        <span class="section-icon">📊</span>
        NMEA CEP 계산기
    </h1>
    
    <div class="calculation-mode-section">
      <div class="mode-selector">
        <label class="mode-label">
          <input type="radio" v-model="calculationMode" value="stationary" />
          정지상태 CEP 계산
        </label>
        <label class="mode-label">
          <input type="radio" v-model="calculationMode" value="moving" />
          이동상태 CEP 계산
        </label>
      </div>
      <div v-if="calculationMode === 'moving'" class="filter-options">
        <label class="filter-label">
          <input type="checkbox" v-model="useFixOnly" />
          FIX 데이터만 사용
        </label>
        <div v-if="useFixOnly" class="fix-info">
          <span>레퍼런스 FIX: {{ referenceFixCount }} / {{ referencePoints.length }}</span>
          <span>측정 FIX: {{ testFixCount }} / {{ testPoints.length }}</span>
        </div>
      </div>
    </div>

    <div class="section-card">      
      <div class="file-upload-section">
        <template v-if="calculationMode === 'stationary'">
          <div class="file-upload-group">
            <label class="file-upload-button">
              <span class="file-upload-text">측정 GPS 파일 선택</span>
              <input type="file" accept=".txt,.log" @change="handleTestFileUpload" class="file-input" />
            </label>
            <span v-if="testFileName" class="selected-filename"> {{ testFileName }}</span>
          </div>
        </template>
        <template v-else>
          <div class="file-upload-group">
            <label class="file-upload-button">
              <span class="file-upload-text">레퍼런스 GPS 파일 선택</span>
              <input type="file" accept=".txt,.log" @change="handleReferenceFileUpload" class="file-input" />
            </label>
            <span v-if="referenceFileName" class="selected-filename"> {{ referenceFileName }}</span>
          </div>
          <div class="file-upload-group">
            <label class="file-upload-button">
              <span class="file-upload-text">측정 GPS 파일 선택</span>
              <input type="file" accept=".txt,.log" @change="handleTestFileUpload" class="file-input" />
            </label>
            <span v-if="testFileName" class="selected-filename"> {{ testFileName }}</span>
          </div>
        </template>
      </div>
    </div>

    <h2 class="font-semibold">📊 위치 데이터</h2>
    <transition name="fade">      
      <div v-if="points.length" class="section-card">
        <div v-if="calculationMode === 'stationary'" class="center-point-info">
          중심점: 위도 {{ center.lat.toFixed(6) }}, 경도 {{ center.lon.toFixed(6) }}
        </div>
        <div class="scrollable-container">
          <table class="data-table">
            <thead>
              <tr class="header-row">
                <th class="header-cell">#</th>
                <th class="header-cell">시간</th>
                <template v-if="calculationMode === 'moving'">
                  <th class="header-cell">레퍼런스 위도</th>
                  <th class="header-cell">레퍼런스 경도</th>
                </template>
                <th class="header-cell">측정 위도</th>
                <th class="header-cell">측정 경도</th>
                <th class="header-cell">PACC_3D (m)</th>
                <th class="header-cell">PACC_H (m)</th>
                <th class="header-cell">PACC_V (m)</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(p, i) in points" :key="i" class="data-row">
                <td class="data-cell">{{ i + 1 }}</td>
                <td class="data-cell">{{ formatTime(p.test.time) }}</td>
                <template v-if="calculationMode === 'moving'">
                  <td class="data-cell">{{ p.reference.lat.toFixed(6) }}</td>
                  <td class="data-cell">{{ p.reference.lon.toFixed(6) }}</td>
                </template>
                <td class="data-cell">{{ p.test.lat.toFixed(6) }}</td>
                <td class="data-cell">{{ p.test.lon.toFixed(6) }}</td>
                <td class="data-cell">{{ p.dist3d.toFixed(3) }}</td>
                <td class="data-cell">{{ p.distH.toFixed(3) }}</td>
                <td class="data-cell">{{ p.distV.toFixed(3) }}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div v-else class="empty-state">
        파일을 업로드하면 분석 결과가 여기에 표시됩니다.
      </div>
    </transition>
  
    <h2 class="mt-6 font-semibold">📈 시각화</h2>
    <transition name="fade">  
      <div v-if="points.length" class="section-card">           
        <div v-if="calculationMode === 'stationary'" class="chart-container">
          <canvas ref="chartRef"></canvas>
        </div>
        <div v-else class="map-container">
          <l-map
            ref="map"
            v-model:zoom="zoom"
            :center="mapCenter"
            :use-global-leaflet="false"
            class="map"
            @ready="onMapReady"
          >
            <l-tile-layer
              url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
              layer-type="base"
              name="OpenStreetMap"
            />
            <!-- 레퍼런스 GPS 트랙 -->
            <l-polyline
              :lat-lngs="referenceTrack"
              color="blue"
              :weight="3"
              :opacity="0.7"
            />
            <!-- 테스트 GPS 트랙 -->
            <l-polyline
              :lat-lngs="testTrack"
              color="red"
              :weight="3"
              :opacity="0.7"
            />
            <!-- 레퍼런스 GPS 포인트 -->
            <l-circle-marker
              v-for="(point, index) in referencePoints"
              :key="'ref-'+index"
              :lat-lng="[point.lat, point.lon]"
              :radius="4"
              color="blue"
              :fill="true"
              :fill-color="'blue'"
              :fill-opacity="0.6"
            >
              <l-popup>
                시간: {{ formatTime(point.time) }}<br>
                위도: {{ point.lat.toFixed(6) }}<br>
                경도: {{ point.lon.toFixed(6) }}
              </l-popup>
            </l-circle-marker>
            <!-- 테스트 GPS 포인트 -->
            <l-circle-marker
              v-for="(point, index) in testPoints"
              :key="'test-'+index"
              :lat-lng="[point.lat, point.lon]"
              :radius="4"
              color="red"
              :fill="true"
              :fill-color="'red'"
              :fill-opacity="0.6"
            >
              <l-popup>
                시간: {{ formatTime(point.time) }}<br>
                위도: {{ point.lat.toFixed(6) }}<br>
                경도: {{ point.lon.toFixed(6) }}
              </l-popup>
            </l-circle-marker>
          </l-map>
          <!-- 지도 범례 추가 -->
          <div class="map-legend">
            <div class="legend-title">GPS 데이터 범례</div>
            <div class="legend-item">
              <div class="legend-color" style="background-color: blue;"></div>
              <div class="legend-label">레퍼런스 GPS</div>
            </div>
            <div class="legend-item">
              <div class="legend-color" style="background-color: red;"></div>
              <div class="legend-label">측정 GPS</div>
            </div>
            <div class="legend-info">
              <div class="info-item">• 실선: GPS 트랙</div>
              <div class="info-item">• 원형 마커: GPS 포인트</div>
              <div class="info-item">• 클릭: 상세 정보 표시</div>
            </div>
          </div>
        </div>
      </div>
      <div v-else class="empty-state">
        파일을 업로드하면 분석 결과가 여기에 표시됩니다.
      </div>
    </transition>

    <h2 class="mt-6 font-semibold">📌 통계</h2>
    <transition name="fade">  
      <div v-if="points.length">
        <table class="stats-table">
          <thead >
            <tr>
              <th>ITEMS</th> 
              <th>CEP50</th>            
              <th>CEP68</th>
              <th>CEP95</th>
              <th>CEP99</th>
              <th>최소 거리</th>            
              <th>최대 거리</th>
              <th>평균 거리</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>PACC_3D</td>
              <td>{{ accuracyTypes.pacc_3d.cep50.toFixed(3) }} m</td>
              <td>{{ accuracyTypes.pacc_3d.cep68.toFixed(3) }} m</td>
              <td>{{ accuracyTypes.pacc_3d.cep95.toFixed(3) }} m</td>
              <td>{{ accuracyTypes.pacc_3d.cep99.toFixed(3) }} m</td>
              <td>{{ accuracyTypes.pacc_3d.min.toFixed(3) }} m</td>
              <td>{{ accuracyTypes.pacc_3d.max.toFixed(3) }} m</td>
              <td>{{ accuracyTypes.pacc_3d.avg.toFixed(3) }} m</td>
            </tr>
            <tr>
              <td>PACC_H</td>
              <td>{{ accuracyTypes.pacc_h.cep50.toFixed(3) }} m</td>
              <td>{{ accuracyTypes.pacc_h.cep68.toFixed(3) }} m</td>
              <td>{{ accuracyTypes.pacc_h.cep95.toFixed(3) }} m</td>
              <td>{{ accuracyTypes.pacc_h.cep99.toFixed(3) }} m</td>
              <td>{{ accuracyTypes.pacc_h.min.toFixed(3) }} m</td>
              <td>{{ accuracyTypes.pacc_h.max.toFixed(3) }} m</td>
              <td>{{ accuracyTypes.pacc_h.avg.toFixed(3) }} m</td>
            </tr><tr>
              <td>PACC_V</td>
              <td>{{ accuracyTypes.pacc_v.cep50.toFixed(3) }} m</td>
              <td>{{ accuracyTypes.pacc_v.cep68.toFixed(3) }} m</td>
              <td>{{ accuracyTypes.pacc_v.cep95.toFixed(3) }} m</td>
              <td>{{ accuracyTypes.pacc_v.cep99.toFixed(3) }} m</td>
              <td>{{ accuracyTypes.pacc_v.min.toFixed(3) }} m</td>
              <td>{{ accuracyTypes.pacc_v.max.toFixed(3) }} m</td>
              <td>{{ accuracyTypes.pacc_v.avg.toFixed(3) }} m</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div v-else class="empty-state">
        파일을 업로드하면 분석 결과가 여기에 표시됩니다.
      </div>
    </transition>
  </div>
</template>
  
<script setup>
  import { ref, nextTick, watch, computed, onMounted } from 'vue'
  import { Chart, ScatterController, LinearScale, PointElement, Title, Tooltip } from 'chart.js'
  import "leaflet/dist/leaflet.css"
  import { LMap, LTileLayer, LPolyline, LCircleMarker, LPopup } from "@vue-leaflet/vue-leaflet"
  
  Chart.register(ScatterController, LinearScale, PointElement, Title, Tooltip)
  
  const points = ref([])
  const referencePoints = ref([])
  const testPoints = ref([])
  const accuracyTypes = ref({
    pacc_3d: { cep50: 0, cep68: 0, cep95: 0, cep99: 0, min: 0, max: 0, avg: 0 },
    pacc_h: { cep50: 0, cep68: 0, cep95: 0, cep99: 0, min: 0, max: 0, avg: 0 },
    pacc_v: { cep50: 0, cep68: 0, cep95: 0, cep99: 0, min: 0, max: 0, avg: 0 }
  })
  const referenceFileName = ref('')
  const testFileName = ref('')
  const center = ref({ lat: 0, lon: 0 })
  const chartRef = ref()
  let chartInstance = null
  const calculationMode = ref('stationary') // 'stationary' or 'moving'

  // 지도 관련 상태
  const zoom = ref(15)
  const map = ref(null)
  const isMapReady = ref(false)

  const mapCenter = computed(() => {
    if (points.value.length === 0) return [37.5665, 126.9780] // 서울 좌표
    const firstPoint = points.value[0]
    return [firstPoint.reference.lat, firstPoint.reference.lon]
  })

  // 트랙 데이터 계산
  const referenceTrack = computed(() => 
    points.value.map(p => [p.reference.lat, p.reference.lon]),
  )

  const testTrack = computed(() => 
    points.value.map(p => [p.test.lat, p.test.lon]),
  )

  // 지도 초기화 및 업데이트
  const updateMapSize = async () => {
    if (!isMapReady.value || !map.value?.leafletObject) return
    
    await nextTick()
    try {
      map.value.leafletObject.invalidateSize()
    } catch (error) {
      console.warn('지도 업데이트 중 오류 발생:', error)
    }
  }

  // 지도가 준비되면 호출되는 함수
  const onMapReady = () => {
    isMapReady.value = true
    updateMapSize()
  }

  // 데이터나 계산 모드가 변경될 때 지도 업데이트
  watch([points, calculationMode], async () => {
    if (calculationMode.value === 'moving' && points.value.length > 0) {
      await updateMapSize()
    }
  }, { deep: true })

  // 컴포넌트가 마운트된 후 지도 업데이트
  onMounted(() => {
    if (calculationMode.value === 'moving' && points.value.length > 0) {
      updateMapSize()
    }
  })

  // FIX 필터링 관련 상태 추가
  const useFixOnly = ref(false)
  const referenceFixCount = ref(0)
  const testFixCount = ref(0)

  // NMEA 데이터 파싱 함수 수정
  function parseNmeaData(text) {
    const lines = text.split(/\r?\n/)
    const points = []
    let alt = 0
    let time = 0
    let fix = false
    let fixQuality = 0

    for (const line of lines) {
      let latRaw = '', latDir = '', lonRaw = '', lonDir = ''
      fix = false
      fixQuality = 0

      if (line.startsWith('$GNGGA') || line.startsWith('$GPGGA')) {
        const parts = line.split(',')
        latRaw = parts[2]
        latDir = parts[3]
        lonRaw = parts[4]
        lonDir = parts[5]
        alt = !isNaN(parseFloat(parts[9])) ? parseFloat(parts[9]) : 0
        time = parseTime(parts[1])
        // FIX 상태 확인 (0: No fix, 1: GPS fix, 2: DGPS fix, 4: RTK fixed, 5: RTK float)
        fixQuality = parseInt(parts[6])
        fix = fixQuality > 0
      } else if (line.startsWith('$GNRMC') || line.startsWith('$GPRMC')) {
        const parts = line.split(',')
        latRaw = parts[3]
        latDir = parts[4]
        lonRaw = parts[5]
        lonDir = parts[6]
        time = parseTime(parts[1])
        // RMC의 상태 필드 확인 (A: Valid, V: Invalid)
        fix = parts[2] === 'A'
      }

      const lat = convertToDecimal(latRaw, latDir)
      const lon = convertToDecimal(lonRaw, lonDir)

      if (!isNaN(lat) && !isNaN(lon) && lat !== 0 && lon !== 0) {
        points.push({ lat, lon, alt, time, fix, fixQuality })
      }
    }

    return points
  }

  // FIX 카운트 업데이트 함수
  function updateFixCounts() {
    referenceFixCount.value = referencePoints.value.filter(p => p.fixQuality === 4).length
    testFixCount.value = testPoints.value.filter(p => p.fixQuality === 4).length
  }

  // 파일 업로드 핸들러 수정
  function handleReferenceFileUpload(event) {
    const file = event.target.files[0]
    if (!file) return
    referenceFileName.value = file.name
    const reader = new FileReader()
    reader.onload = () => {
      const cleanedText = reader.result.replace(/\0/g, '')
      referencePoints.value = parseNmeaData(cleanedText)
      updateFixCounts()
      if (testPoints.value.length > 0) {
        calculateMovingCep()
      }
    }
    reader.readAsText(file)
  }

  function handleTestFileUpload(event) {
    const file = event.target.files[0]
    if (!file) return
    testFileName.value = file.name
    const reader = new FileReader()
    reader.onload = () => {
      const cleanedText = reader.result.replace(/\0/g, '')
      testPoints.value = parseNmeaData(cleanedText)
      updateFixCounts()
      
      if (calculationMode.value === 'stationary') {
        const avgLat = testPoints.value.reduce((s, p) => s + p.lat, 0) / testPoints.value.length
        const avgLon = testPoints.value.reduce((s, p) => s + p.lon, 0) / testPoints.value.length
        const avgAlt = testPoints.value.reduce((s, p) => s + p.alt, 0) / testPoints.value.length
        center.value = { lat: avgLat, lon: avgLon, alt: avgAlt }
        calculateStationaryCep()
      } else if (referencePoints.value.length > 0) {
        calculateMovingCep()
      }
    }
    reader.readAsText(file)
  }

  function calculateStationaryCep() {
    // 정지상태 CEP 계산 (단일 파일)
    const calculatedPoints = testPoints.value.map(p => ({
      reference: { lat: center.value.lat, lon: center.value.lon, alt: center.value.alt, time: p.time },
      test: { lat: p.lat, lon: p.lon, alt: p.alt, time: p.time },
      dist3d: calculate3DDistance(center.value, p),
      distH: calculateHDistance(center.value, p),
      distV: calculateVDistance(center.value, p)
    }))

    points.value = calculatedPoints

    // CEP 계산
    accuracyTypes.value.pacc_3d = calculateAccuracyStats(calculatedPoints.map(p => p.dist3d))
    accuracyTypes.value.pacc_h = calculateAccuracyStats(calculatedPoints.map(p => p.distH))
    accuracyTypes.value.pacc_v = calculateAccuracyStats(calculatedPoints.map(p => p.distV))

    if (calculatedPoints.length > 0) {
      nextTick(() => drawChart())
    }
  }

  function calculateMovingCep() {
    // RTK fixed(status 4) 데이터만 필터링
    const filteredRefPoints = useFixOnly.value 
      ? referencePoints.value.filter(p => p.fixQuality === 4)
      : referencePoints.value
    const filteredTestPoints = useFixOnly.value
      ? testPoints.value.filter(p => p.fixQuality === 4)
      : testPoints.value

    // 시간순으로 데이터 정렬
    const sortedRefPoints = [...filteredRefPoints].sort((a, b) => a.time - b.time)
    const sortedTestPoints = [...filteredTestPoints].sort((a, b) => a.time - b.time)
    
    const matchedPoints = []
    const timeMap = new Map() // 시간별 테스트 포인트 매핑
    
    // 테스트 포인트를 시간별로 맵에 저장
    sortedTestPoints.forEach(point => {
        const timeKey = Math.round(point.time * 10) / 10 // 0.1초 단위로 반올림
        if (!timeMap.has(timeKey)) {
            timeMap.set(timeKey, [])
        }
        timeMap.get(timeKey).push(point)
    })
    
    // 레퍼런스 포인트와 정확히 일치하는 시간의 테스트 포인트 매칭
    for (const refPoint of sortedRefPoints) {
        const timeKey = Math.round(refPoint.time * 10) / 10
        const matchingTestPoints = timeMap.get(timeKey)
        
        if (matchingTestPoints && matchingTestPoints.length > 0) {
            // 같은 시간에 여러 테스트 포인트가 있는 경우, 가장 가까운 포인트 선택
            const bestMatch = matchingTestPoints.reduce((closest, testPoint) => {
                const dist = calculate3DDistance(refPoint, testPoint)
                return (!closest || dist < closest.distance) 
                    ? { point: testPoint, distance: dist }
                    : closest
            }, null)
            
            if (bestMatch) {
                matchedPoints.push({
                    reference: refPoint,
                    test: bestMatch.point,
                    dist3d: calculate3DDistance(refPoint, bestMatch.point),
                    distH: calculateHDistance(refPoint, bestMatch.point),
                    distV: calculateVDistance(refPoint, bestMatch.point),
                    time: refPoint.time
                })
            }
        }
    }

    // 매칭된 포인트가 너무 적으면 경고
    if (matchedPoints.length < sortedRefPoints.length * 0.3) {
        console.warn('매칭된 포인트가 전체 데이터의 30% 미만입니다. 데이터 수집 주기와 시간 동기화를 확인해주세요.')
    }

    // 시간별 거리 차이 분석을 위한 데이터 준비
    const timeSeriesData = matchedPoints.map(p => ({
        time: p.time,
        dist3d: p.dist3d,
        distH: p.distH,
        distV: p.distV
    }))

    // 시간별 거리 차이의 통계 계산
    const calculateTimeSeriesStats = (data) => {
        const distances = data.map(d => d.dist3d)
        const mean = distances.reduce((sum, val) => sum + val, 0) / distances.length
        const stdDev = Math.sqrt(
            distances.reduce((sum, val) => sum + Math.pow(val - mean, 2), 0) / distances.length
        )
        
        return {
            mean,
            stdDev,
            max: Math.max(...distances),
            min: Math.min(...distances),
            timeRange: {
                start: data[0].time,
                end: data[data.length - 1].time
            }
        }
    }

    const timeStats = calculateTimeSeriesStats(timeSeriesData)
    console.log('시간별 거리 차이 통계:', {
        평균거리: timeStats.mean.toFixed(3) + 'm',
        표준편차: timeStats.stdDev.toFixed(3) + 'm',
        최대거리: timeStats.max.toFixed(3) + 'm',
        최소거리: timeStats.min.toFixed(3) + 'm',
        시간범위: `${formatTime(timeStats.timeRange.start)} - ${formatTime(timeStats.timeRange.end)}`
    })

    points.value = matchedPoints

    // CEP 계산
    if (matchedPoints.length > 0) {
        // 이상치 제거 (평균에서 3 표준편차 이상 벗어난 값)
        const filteredDistances = timeSeriesData
            .filter(d => Math.abs(d.dist3d - timeStats.mean) <= 3 * timeStats.stdDev)
            .map(d => d.dist3d)

        accuracyTypes.value.pacc_3d = calculateAccuracyStats(filteredDistances)
        accuracyTypes.value.pacc_h = calculateAccuracyStats(matchedPoints.map(p => p.distH))
        accuracyTypes.value.pacc_v = calculateAccuracyStats(matchedPoints.map(p => p.distV))
        
        // 중심점 업데이트 (첫 번째 레퍼런스 포인트 기준)
        center.value = {
            lat: matchedPoints[0].reference.lat,
            lon: matchedPoints[0].reference.lon,
            alt: matchedPoints[0].reference.alt
        }
        
        nextTick(() => drawChart())
    }
  }
  
  function calculateAccuracyStats(distances) {
    // 이상치 제거 (평균에서 3 표준편차 이상 벗어난 값)
    const mean = distances.reduce((sum, val) => sum + val, 0) / distances.length
    const stdDev = Math.sqrt(distances.reduce((sum, val) => sum + Math.pow(val - mean, 2), 0) / distances.length)
    const filteredDistances = distances.filter(d => Math.abs(d - mean) <= 3 * stdDev)
    
    const sorted = filteredDistances.sort((a, b) => a - b)
    const len = sorted.length
    
    // 정확한 인덱스 계산
    const idx50 = Math.round(len * 0.50) - 1
    const idx68 = Math.round(len * 0.68) - 1
    const idx95 = Math.round(len * 0.95) - 1
    const idx99 = Math.round(len * 0.99) - 1
    
    return {
        cep50: sorted[idx50] || 0,
        cep68: sorted[idx68] || 0,
        cep95: sorted[idx95] || 0,
        cep99: sorted[idx99] || 0,
        min: sorted[0] || 0,
        max: sorted[sorted.length - 1] || 0,
        avg: sorted.reduce((sum, v) => sum + v, 0) / len || 0
    }
}

  function convertToDecimal(raw, dir) {
    if (!raw || !dir) return NaN
    let degrees, minutes
    if (dir === 'N' || dir === 'S') {
      degrees = parseInt(raw.substring(0, 2))
      minutes = parseFloat(raw.substring(2))
    } else {
      degrees = parseInt(raw.substring(0, 3))
      minutes = parseFloat(raw.substring(3))
    }
    let decimal = degrees + minutes / 60
    return (dir === 'S' || dir === 'W') ? -decimal : decimal
  }
  
  function formatTime(seconds) {
    const hours = Math.floor(seconds / 3600)
    const minutes = Math.floor((seconds % 3600) / 60)
    const secs = Math.floor(seconds % 60)
    return `${hours.toString().padStart(2, '0')}:${minutes.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`
  }

  function drawChart() {
    if (!chartRef.value) return
    if (chartInstance) chartInstance.destroy()

    const referenceData = points.value.map(p => ({
      x: calculateLambertDistance(center.value, p.reference) * (p.reference.lon > center.value.lon ? 1 : -1),
      y: calculateLambertDistance(center.value, p.reference) * (p.reference.lat > center.value.lat ? 1 : -1),
      lat: p.reference.lat.toFixed(6),
      lon: p.reference.lon.toFixed(6)
    }))

    const testData = points.value.map(p => ({
      x: calculateLambertDistance(center.value, p.test) * (p.test.lon > center.value.lon ? 1 : -1),
      y: calculateLambertDistance(center.value, p.test) * (p.test.lat > center.value.lat ? 1 : -1),
      lat: p.test.lat.toFixed(6),
      lon: p.test.lon.toFixed(6),
      dist3d: p.dist3d.toFixed(3)
    }))

    // Find max range for square aspect ratio
    const maxRange = Math.max(
      ...referenceData.map(p => Math.abs(p.x)),
      ...referenceData.map(p => Math.abs(p.y)),
      ...testData.map(p => Math.abs(p.x)),
      ...testData.map(p => Math.abs(p.y))
    ) * 1.2 // Add 20% padding

    chartInstance = new Chart(chartRef.value, {
      type: 'scatter',
      data: {
        datasets: [{
          label: calculationMode.value === 'stationary' ? 'Center Point' : 'Reference GPS',
          data: referenceData,
          backgroundColor: 'rgba(0, 0, 255, 0.6)',
          pointRadius: 4
        },
        {
          label: 'Test GPS',
          data: testData,
          backgroundColor: 'rgba(255, 0, 0, 0.6)',
          pointRadius: 4
        }]
      },
      options: {
        aspectRatio: 1,
        responsive: true,
        interaction: {
          mode: 'nearest',
          axis: 'xy',
          intersect: true
        },
        plugins: {
          tooltip: {
            enabled: true,
            mode: 'point',
            intersect: true,
            position: 'nearest',
            filter: function(tooltipItem) {
              // 가장 가까운 포인트만 표시
              return tooltipItem.datasetIndex === 0 || tooltipItem.datasetIndex === 1;
            },
            callbacks: {
              label: function(context) {
                const data = context.raw
                if (context.dataset.label === 'Test GPS') {
                  return `위도: ${data.lat}, 경도: ${data.lon}, 거리: ${data.dist3d}m`
                } else {
                  return `위도: ${data.lat}, 경도: ${data.lon}`
                }
              }
            }
          },
          title: { 
            display: true, 
            text: 'Position Scatter Plot with CEP Circles',
            padding: 20
          },
          legend: {
            display: true,
            position: 'bottom'
          },
          cepCircles: {
            radii: [
              {value: accuracyTypes.value.pacc_3d.cep50, label: 'CEP50'},
              {value: accuracyTypes.value.pacc_3d.cep68, label: 'CEP68'},
              {value: accuracyTypes.value.pacc_3d.cep95, label: 'CEP95'},
              {value: accuracyTypes.value.pacc_3d.cep99, label: 'CEP99'}
            ]
          }
        },
        scales: {
          x: {
            title: { display: true, text: 'East-West (meters)' },
            min: -maxRange,
            max: maxRange,
            grid: {
              color: 'rgba(0, 0, 0, 0.1)',
              drawTicks: true,
              drawOnChartArea: true
            },
            ticks: {
              stepSize: maxRange/5
            }
          },
          y: {
            title: { display: true, text: 'North-South (meters)' },
            min: -maxRange,
            max: maxRange,
            grid: {
              color: 'rgba(0, 0, 0, 0.1)',
              drawTicks: true,
              drawOnChartArea: true
            },
            ticks: {
              stepSize: maxRange/5
            }
          }
        }
      },
      plugins: [{
        id: 'cepCircles',
        beforeDatasetsDraw(chart, args, pluginOptions) {
          const { ctx, scales } = chart
          const centerX = scales.x.getPixelForValue(0)
          const centerY = scales.y.getPixelForValue(0)
          const radii = pluginOptions.radii || []
          const colors = ['red', 'blue', 'orange', 'green']

          ctx.save()
          radii.forEach((r, i) => {
            const radiusInPixels = Math.abs(scales.x.getPixelForValue(r.value) - centerX)
            
            ctx.beginPath()
            ctx.arc(centerX, centerY, radiusInPixels, 0, 2 * Math.PI)
            ctx.strokeStyle = colors[i].replace('0.2', '1.0')
            ctx.lineWidth = 2
            ctx.stroke()
            
            const angle = Math.PI / 4
            const labelX = centerX + (radiusInPixels * Math.cos(angle))
            const labelY = centerY - (radiusInPixels * Math.sin(angle))
            
            ctx.fillStyle = colors[i].replace('0.2', '1.0')
            ctx.font = '12px Arial'
            ctx.textAlign = 'left'
            ctx.fillText(`${r.label}: ${r.value.toFixed(2)}m`, labelX, labelY)
          })
          ctx.restore()
        }
      }]
    })
  }

  // 계산 모드 변경 시 FIX 카운트 초기화
  watch(calculationMode, () => {
    // ... existing reset code ...
    referenceFixCount.value = 0
    testFixCount.value = 0
  })

  // FIX 필터링 옵션 변경 시 재계산
  watch(useFixOnly, async () => {
    if (calculationMode.value === 'moving' && points.value.length > 0) {
      await calculateMovingCep()
      // 차트와 지도 업데이트
      if (chartInstance) {
        chartInstance.destroy()
        chartInstance = null
      }
      await nextTick()
      drawChart()
      if (isMapReady.value && map.value?.leafletObject) {
        await updateMapSize()
      }
    }
  })

  // 유틸리티 함수들 정의
  function parseTime(timeStr) {
    if (!timeStr) return 0
    const hours = parseInt(timeStr.substring(0, 2))
    const minutes = parseInt(timeStr.substring(2, 4))
    const seconds = parseFloat(timeStr.substring(4))
    return hours * 3600 + minutes * 60 + seconds
  }

  function calculateLambertDistance(point1, point2) {
    const R = 6371000 // 지구 반경 (미터)
    const lat1 = point1.lat * Math.PI / 180
    const lat2 = point2.lat * Math.PI / 180
    const lon1 = point1.lon * Math.PI / 180
    const lon2 = point2.lon * Math.PI / 180
    
    const dLat = lat2 - lat1
    const dLon = lon2 - lon1
    
    const a = Math.sin(dLat/2) * Math.sin(dLat/2) +
              Math.cos(lat1) * Math.cos(lat2) *
              Math.sin(dLon/2) * Math.sin(dLon/2)
    const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a))
    
    return R * c
  }

  function calculate3DDistance(point1, point2) {
    const horizontalDist = calculateLambertDistance(point1, point2)
    const verticalDist = Math.abs(point1.alt - point2.alt)
    return Math.sqrt(horizontalDist * horizontalDist + verticalDist * verticalDist)
  }

  function calculateHDistance(point1, point2) {
    return calculateLambertDistance(point1, point2)
  }

  function calculateVDistance(point1, point2) {
    return Math.abs(point1.alt - point2.alt)
  }

</script>
  
